import { CircularProgress, TextField, Box } from '@mui/material';
import { styled } from '@mui/system';
import { Link } from 'react-router-dom';
import { useState, useEffect } from 'react';


const Component = ({ onChange, inputChars = '........' }) => {

    const [chars, setChars] = useState(inputChars)
    useEffect(() => {
        onChange && onChange(inputChars)
    }, [inputChars])
    console.log(inputChars)
    return (
        <Box display="flex" flexDirection={"row"}>
            {
                inputChars
                    .split('')
                    .map((c, i) => <Box key={i} width="40px"><TextField
                        inputProps={{
                            maxLength: 1
                        }}
                        value={inputChars[i]}
                        onFocus={e => e.target.select()}
                        onChange={e => {
                            const o = [...inputChars]
                            o[i] = e.target.value || '.'
                            onChange && onChange(o.join(''))
                        }}

                    /></Box>)
            }
        </Box >

    );
};

export default Component;